<script>

import ConfirmationOfActionsModal from '../components/СonfirmationOfActionsModal';

export default {
  name: 'Table',
  components: {
    ConfirmationOfActionsModal,
  },

  props: {
    pointsEvent: {
      required: true,
    },
    pointList: {
      required: true,
    },
    page: {
      required: true,
    },
    sort: {
      required: true,
    },
  },

  data() {
    return {
      showConfirmationModal: false,
      newPointList: null,
      perPages: [5, 10, 20, 50, 100],
      perPage: 20,
      tableHeader: [
        { name: 'MAC-адрес', value: 'mac' },
        { name: 'Имя', value: 'name' },
        { name: 'Производитель', value: 'vendor' },
        { name: 'Время активности', value: 'lastDateTime' },
        { name: 'Кол-во пакетов', value: 'packages' },
        { name: 'Номер канала', value: 'frequency' },
        { name: 'Уровень сигнала', value: 'level' },
        { name: 'Список', value: 'pointList' },
      ],
    };
  },

  methods: {

    changeShowConfirmationModal() {
      this.showConfirmationModal = !this.showConfirmationModal;
    },

    updatePointList(event, { id }) {
      this.changeShowConfirmationModal();
      this.newPointList = { pointId: id, pointListId: event };
    },

    yes() {
      this.$emit('updatePointList', this.newPointList);
    },

    no() {
      this.$emit('updatePoint');
    },

    changeCurrentPage(currentPage) {
      if (this.page.currentPage !== currentPage) {
        this.page.currentPage = currentPage;
        this.$emit('update:page', this.page);
        this.$emit('updatePoint');
      }
    },

    changePerPage(perPage) {
      if (this.page.perPage !== perPage) {
        this.page.perPage = perPage;
        this.page.currentPage = 1;
        this.$emit('update:page', this.page);
        this.$emit('updatePoint');
      }
    },

    changeSort(field) {
      if (!field) {
        return;
      }
      if (field === this.sort.field) {
        this.sort.direction = this.sort.direction === 'asc' ? 'desc' : 'asc';
      } else {
        this.sort.field = field;
        this.sort.direction = 'asc';
      }
      this.$emit('update:sort', this.sort);
      this.$emit('updatePoint');
    },
  },
};
</script>

<template>
  <div>
    <table
      class="table table-hover color"
    >
      <thead class="thead-light">
        <tr>
          <th class="timeFrame" />
          <th
            v-for="header in tableHeader"
            :key="header.value"
          >
            <div @click="changeSort(header.value)">
              {{ header.name }}
            </div>
            <div
              class="sort"
              :class="sort.field === header.value ? sort.direction : ''"
            />
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="point in pointsEvent"
          :key="point.id"
        >
          <td
            class="timeFrame"
            :class="point.timeFrame"
          />
          <td>{{ point.mac }}</td>
          <td>{{ point.name }}</td>
          <td>{{ point.vendor }}</td>
          <td>{{ point.lastDateTime }}</td>
          <!--<td>{{ point.coding }}</td>-->
          <!--<td>{{ point.standard }}</td>-->
          <td>{{ point.packages }}</td>
          <td>{{ point.frequency }}</td>
          <td>{{ point.level }}</td>
          <td>
            <b-form-select
              v-model="point.pointList.id"
              :options="pointList"
              @change="updatePointList($event, point)"
            />
          </td>
        </tr>
      </tbody>
    </table>
    <b-row>
      <b-col cols="11">
        <b-pagination
          v-model="page.currentPage"
          :total-rows="page.rows"
          :per-page="page.perPage"
          size="md"
          align="center"
          @change="changeCurrentPage"
        />
      </b-col>
      <b-col>
        <b-select
          v-model="page.perPage"
          :options="perPages"
          @change="changePerPage"
        />
      </b-col>
    </b-row>
    <confirmation-of-actions-modal
      :show-modal.sync="showConfirmationModal"
      text-confirmation="Переместить Wi-Fi точку доступа в выбранный список?"
      @yes="yes"
      @no="no"
    />
  </div>
</template>

